import { Logo } from "..";
import { AnimatePresence, motion } from "framer-motion";
import { RiMenu3Line } from "react-icons/ri";
import { MdSearch, MdOutlineClose } from "react-icons/md";
import { useState } from "react";

const TopNav = () => {
  const [searchActive, setSearchActive] = useState(true);

  const ToggleSearch = () => {
    setSearchActive((prev) => !prev);
  };

  const searchContainer = {
    hidden: {
      originY: 0,
      scaleY: 0,
    },
    shown: {
      originY: 0,
      scaleY: 1,
      transition: {
        duration: 0.2,
        ease: "easeOut",
      },
    },
  };

  const iconContainer = {
    hidden: {
      opacity: 0,
    },
    shown: {
      opacity: 1,
      transition: {
        duration: 0.2,
        ease: "easeOut",
      },
    },
  };

  return (
    <AnimatePresence>
      <nav className="fixed top-0 w-screen">
        {searchActive ? (
          //search Active
          <div className="w-full relative">
            <div className="w-full p-4 bg-primary flex justify-between items-center">
              <Logo width={8} />
              <motion.div
                variants={iconContainer}
                initial="hidden"
                animate="shown"
                exit="hidden"
                className="flex space-x-2"
              >
                <MdSearch onClick={ToggleSearch} color="White" size="2em" />
                <MdOutlineClose color="White" size="2em" />
              </motion.div>
            </div>
            <motion.div
              variants={searchContainer}
              initial="hidden"
              animate="shown"
              className="h-screen w-full overflow-y-scroll bg-black absolute top-30 opacity-80"
            ></motion.div>
          </div>
        ) : (
          //search Inactive
          <div className="w-screen p-4 bg-primary flex justify-between items-center">
            <Logo width={8} />
            <motion.div
              variants={iconContainer}
              initial="hidden"
              animate="shown"
              exit="hidden"
              className="flex space-x-2"
            >
              <MdSearch onClick={ToggleSearch} color="White" size="2em" />
              <RiMenu3Line color="White" size="2em" />
            </motion.div>
          </div>
        )}
      </nav>
    </AnimatePresence>
  );
};

export default TopNav;
